<template>
    <div class="g-view">
        <div>首页</div>
        <div class="js-dialog">弹窗</div>
        <g-img-list></g-img-list>
        <g-img-list></g-img-list>
        <g-img-list></g-img-list>
        <g-img-list></g-img-list>
        <g-img-list></g-img-list>
        <g-img-list></g-img-list>
        <g-img-list></g-img-list>
        <g-img-list></g-img-list>
        <g-img-list></g-img-list>
        <g-img-list></g-img-list>
    </div>
</template>

<script>
    export default {
        name: 'home',
        data() {
            return {};
        },
        methods: {},
        components: {
            'g-img-list': function (resolve) {
                require.ensure([], function () {
                    resolve(require('../components-vue/g-img-list.vue'));
                }, 'g-img-list');
            },
        },
        mounted() {
            const Dialog = require('../components-dom/g-dialog');
            document.querySelector('.js-dialog').addEventListener('click', function () {
                new Dialog({
                    callback: {
                        confirm: function () {
                            new Dialog({config: {alert: {icon: 'icon-chenggong', content: '已确认'}}});
                        },
                        cancel: function () {
                            new Dialog({config: {alert: {icon: 'icon-chenggong', content: '已取消'}}});
                        },
                        close: function () {
                            new Dialog({config: {alert: {icon: 'icon-chenggong', content: '已关闭'}}});
                        },
                    },
                    config: {
                        type: 'confirm',
                    },
                });
            });
            console.log('this.$tools\n', this.$tools);
            console.log('this.$applications\n', this.$applications);
            console.log('this.$axios\n', this.$axios);
            console.log('this.$jsonp\n', this.$jsonp);
            console.log('this.$lazyload\n', this.$lazyload);
            console.log('this.$store.state\n', this.$store.state);
            console.log('this.$route\n', this.$route);
        },
    };
</script>

<style scoped lang="scss">
    @import "../scss/config/config";
</style>



